import Button from './components/button' // 
import Checkbox from './components/checkbox' // 
import Radio from './components/radio' // 
import Input from './components/input' // 
import Textarea from './components/textarea' // 
import FileUp from './components/fileup' // 
import Select from './components/select' // 
import List from './components/list' // 
import Dropdown from './components/dropdown' // 
import Tree from './components/tree' // 
import Pages from './components/pages' // 
import Table from './components/table' // 
import Tip from './components/tip' // 
import Toast from './components/toast' // toast
import Grid from './components/grid' // 
import Icon from './components/icon' //  iconfont link
import Message from './components/message' //  
import Scroller from './components/scroller' // 
import Tabs from './components/tabs' // 

import Waves from './components/waves/' // 
// 
import './stylesheets/core.scss'

const options = { Button, Checkbox, Select, Radio, Input, Textarea, FileUp, List, Dropdown, Tree, Pages, Table, Scroller, Grid, Waves, Icon, Tabs }

options.install = (Vue) => {
  for (let component in options) {
    const componentInstaller = options[component]
    if (componentInstaller && component !== 'install') {
      Vue.use(componentInstaller)
    }
  }

  Vue.prototype.$tip = Tip
  Vue.prototype.$toast = Toast
  Vue.prototype.$message = Message
}

export default options



